<template>
  <v-app>
    <v-app-bar color="primary" dark>
      <v-app-bar-nav-icon></v-app-bar-nav-icon>

      <v-toolbar-title>Movie Recommendation Engine</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon @click="changePage">
        <v-icon>
          {{ page === "rate" ? "mdi-movie" : "mdi-star" }}
        </v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <v-container v-if="!username" style="min-height: 90vh;">
        Devam etmek için kullanıcı adı seçin
        <br />
        <v-form>
          <v-text-field
            v-model="name"
            label="Kullanıcı adı"
            required
          ></v-text-field>
          <v-btn color="success" class="mr-4" @click="register">
            Devam Et
          </v-btn>
        </v-form>
      </v-container>

      <Main
        v-else-if="page === 'rate'"
        :username="username"
        :totalRatings="parseInt(totalRatings)"
      />
      <Recommendations v-else :username="username" />
    </v-main>
  </v-app>
</template>

<script>
import Main from "./components/Main";
import Recommendations from "./components/Recommendations";

export default {
  name: "App",
  components: {
    Main,
    Recommendations
  },
  data: () => ({
    name: "",
    username: "",
    totalRatings: 0,
    page: "rate"
  }),
  mounted() {
    let user = localStorage.getItem("username");
    if (user != null && user != undefined) {
      this.username = user;
      this.totalRatings = localStorage.getItem("totalRatings");
    }
  },
  methods: {
    register() {
      localStorage.setItem("username", this.name);
      localStorage.setItem("totalRatings", 0);
      this.username = this.name;
      this.totalRatings = 0;
    },
    changePage() {
      if (!this.username) return;

      if (this.page === "rate") this.page = "rec";
      else this.page = "rate";
    }
  }
};
</script>
