<template>
  <v-container style="min-height: 90vh; padding-top: 24px;">
    <v-row class="text-center">
      <v-col class="mb-4" cols="12">
        <h1 class="display-2 font-weight-bold mb-3">
          MRE
        </h1>

        <p class="subheading font-weight-regular">
          Aşağıdan istediğin filmleri arayıp oy verebilirsin.
        </p>
      </v-col>

      <v-col cols="12">
        <v-row align="center">
          <v-col cols="4">
            <v-text-field v-model="searchFor" label="Film Ara"></v-text-field>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="searchGenre"
              :items="genres"
              label="Kategori"
              return-object
              single-line
            ></v-select>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="sortBy"
              :items="['Popularity', 'Ascending', 'Descending']"
              label="Sort by"
              return-object
              single-line
            ></v-select>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12">
        <v-row>
          <v-col v-for="movie in moviesPaginated" :key="movie.id" cols="2">
            <movie
              :id="parseInt(movie.id)"
              :title="movie.t"
              :rated="getRating(movie.id)"
              @rateMovie="rateMovie"
            />
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12">
        <v-pagination
          v-model="page"
          :length="parseInt(movies.length / 18)"
          :total-visible="10"
        ></v-pagination>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
const moviesJson = require("../assets/movies.json");

import Movie from "./Movie";

export default {
  name: "Main",
  props: {
    username: String,
    totalRatings: Number
  },
  components: { Movie },
  data: () => ({
    movies: [],
    genres: [
      "Action",
      "Adventure",
      "Animation",
      "Comedy",
      "Crime",
      "Documentary",
      "Drama",
      "Family",
      "Fantasy",
      "Foreign",
      "History",
      "Horror",
      "Music",
      "Mystery",
      "Romance",
      "Science Fiction",
      "Thriller",
      "TV Movie",
      "War",
      "Western"
    ],
    page: 1,
    searchGenre: "",
    sortBy: "Popularity",
    searchFor: "",
    rated: {}
  }),
  mounted() {
    this.filterMovies();

    let ratedMovies = localStorage.getItem("rated");
    if (ratedMovies != null && ratedMovies != undefined) {
      this.rated = JSON.parse(ratedMovies);
    }
  },
  watch: {
    sortBy() {
      this.filterMovies();
    },
    searchFor() {
      this.filterMovies();
    },
    searchGenre() {
      this.filterMovies();
    }
  },
  methods: {
    async rateMovie({ id, rating }) {
      // todo fetch
      const response = await fetch(
        "http://localhost:8000/rate-movie?u=" +
          this.username +
          "&m=" +
          id +
          "&r=" +
          rating
      );
      const final = await response.json();
      if (final.statusCode !== 200)
        alert("error while rating: " + JSON.parse(final.result).error);

      console.log(final);

      localStorage.setItem(
        "totalRatings",
        parseInt(localStorage.getItem("totalRatings")) + 1
      );
      this.rated[id] = rating;
      localStorage.setItem("rated", JSON.stringify(this.rated));
    },
    getRating(id) {
      if (this.rated[id] != undefined) return this.rated[id];
      else return -1;
    },
    filterMovies() {
      let raw = moviesJson;

      if (this.searchGenre) {
        const sg = this.searchGenre;
        raw = raw.filter(function(a) {
          try {
            return a.g.toLowerCase().includes(sg.toLowerCase());
          } catch {
            return false;
          }
        });
      }

      if (this.searchFor) {
        const sf = this.searchFor;
        raw = raw.filter(function(a) {
          try {
            return a.t.toLowerCase().includes(sf.toLowerCase());
          } catch {
            return false;
          }
        });
      }

      if (this.sortBy === "Popularity") {
        raw.sort(function(a, b) {
          return b.p - a.p;
        });
      } else if (this.sortBy === "Ascending" || this.sortBy === "Descending") {
        raw.sort(function(a, b) {
          try {
            var nameA = a.t.toUpperCase(); // ignore upper and lowercase
            var nameB = b.t.toUpperCase(); // ignore upper and lowercase
            if (nameA < nameB) {
              return -1;
            }
            if (nameA > nameB) {
              return 1;
            }

            return 0;
          } catch {
            return 0;
          }
        });

        if (this.sortBy === "Descending") raw.reverse();
      }

      this.page = 1;
      this.movies = raw;
    }
  },
  computed: {
    moviesPaginated() {
      return this.movies.slice((this.page - 1) * 18, this.page * 18);
    }
  }
};
</script>
