<template>
  <v-card class="mx-auto my-12" max-width="374">
    <template slot="progress">
      <v-progress-linear
        color="deep-purple"
        height="10"
        indeterminate
      ></v-progress-linear>
    </template>

    <v-card-title class="justify-center">{{ title }}</v-card-title>
    <v-card-actions class="d-flex justify-center">
      <v-progress-linear
        v-if="similarity"
        :value="similarity * 20"
        striped
        height="25"
      >
        <template v-slot:default="{ value }">
          <strong class="white--text"
            >Uyumluluk: {{ Math.ceil(value) }}%</strong
          >
        </template>
      </v-progress-linear>
      <v-rating
        v-else
        v-model="rating"
        :color="rated === -1 && !fetching ? 'amber' : 'green'"
        :readonly="rated != -1 || fetching"
        half-increments
        hover
        length="5"
        size="24"
      ></v-rating>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: "Movie",
  props: {
    id: Number,
    title: String,
    rated: Number,
    similarity: Number
  },
  data: () => ({
    rating: null,
    fetching: false
  }),
  mounted() {
    if (this.rated != -1) this.rating = this.rated;
  },
  watch: {
    rating() {
      if (this.rated != -1) return;
      this.fetching = true;
      this.$emit("rateMovie", { id: this.id, rating: this.rating });
    }
  }
};
</script>
